import { GET_PROFILE, PROFILE_ERROR, LOGOUT } from "../actions/types";

const initialState = { profile: null, loading: true, error: {} };

export default function(state = initialState, action) {
  const { type, payload } = action;
  switch (type) {
    case GET_PROFILE:
      return {
        ...state,
        profile: payload,
        loading: false
      };
    case PROFILE_ERROR:
      return {
        ...state,
        error: payload,
        profile: null,
        loading: false
      };
    case LOGOUT:
      return {
        ...state,
        error: payload,
        profile: null,
        loading: true
      };
    default:
      return state;
  }
}
